body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #666;
  color: #ccc;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
	font-family: 'RazerF5';
	font-style: normal;
	font-weight: 100;
	src: url(./assets/fonts/RazerF5Thin.eot);
	src: local("RazerF5Thin"), url(./assets/fonts/RazerF5Thin.woff) format("woff"), url(./assets/fonts/RazerF5Thin.ttf) format("truetype"), url(./assets/fonts/RazerF5Thin.svg) format("svg");
}
@font-face {
	font-family: 'RazerF5';
	font-style: normal;
	font-weight: 300;
	src: url(./assets/fonts/RazerF5Light.eot);
	src: local("RazerF5Light"), url(./assets/fonts/RazerF5Light.woff) format("woff"), url(./assets/fonts/RazerF5Light.ttf) format("truetype"), url(./assets/fonts/RazerF5Light.svg) format("svg");
}
@font-face {
	font-family: 'RazerF5';
	font-style: normal;
	font-weight: 400;
	src: url(./assets/fonts/RazerF5.eot);
	src: local("RazerF5"), url(./assets/fonts/RazerF5.woff) format("woff"), url(./assets/fonts/RazerF5.ttf) format("truetype"), url(./assets/fonts/RazerF5.svg) format("svg");
}
